body, * {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
.header {
    background-color: purple;
    color: lavender;
    text-align: center;
    box-shadow: px 0 2px rgb(82, 1, 82);
}
.notification {
    position: fixed;
    top: 10%;
    right: 1rem;
    display: inline-block;
    width: 16rem;
    padding: .2rem 0;
    background-color: green;
    color: "white";
    transition: all;
}
.add-todo-form {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 2;
    border: 1px solid;
    border-radius: .8rem;
    background-color: #94b0cc;
    width: 30rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    .close-form {
        position: absolute;
        top: 0;
        right: 1rem;
    }
    form {
        width: 20rem;
        color: white;
        margin-top: 2.5rem;
        margin-bottom: 2rem;
    }
}
.filter-todos {
    margin-top: 1rem;
    display: flex;
    align-items: center;
    gap: .5rem;
    button {
        height: 2.5rem;
    }
}
.none {
    display: none;
}
.todo-list {
    margin: 1rem 0 10rem 5rem;
    padding-bottom: 6rem;
    div {
        display: flex;
        gap: 1rem;
        margin-bottom: .8rem;
        span {
            width: 10rem;
        }
    }
}
.todo-item {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1;
    border: 1px solid;
    border-radius: .8rem;
    background-color: #94b0cc;
    width: 20rem;
    height: 17rem;
    .close-form {
        position: absolute;
        top: 0;
        right: 1rem;
        font-size: 2.5rem;
        cursor: pointer;
        margin-top: -.5rem;
    }
    .todo-data {
        margin-top: .5rem;
        margin-left: 1rem;
        h2 {
            text-align: center;
            text-transform: uppercase;
        }
        button {
            position: absolute;
            bottom: 1rem;
            right: 1rem;
        }
        .description {
            display: -webkit-box; 
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 3;
            max-width: 18rem;
            text-indent: 3rem;
            word-wrap: break-word;
            overflow-y: auto;
        }
    }
}